<template>
  <div class="reg">
    <Titletop title="忘记密码">
      <router-link slot="l" replace :to="{ name: 'login' }"></router-link>
    </Titletop>
    <div class="logo"><img src="@/assets/鲸云小说/10002.png" /></div>
    <Forms>
      <van-form @submit="onSubmit">
        <van-field
          type="phone"
          v-model.trim="phone"
          label="+86"
          placeholder="请输入手机号"
        />
        <div style="display: flex">
          <van-field v-model="sms" center clearable placeholder="短信验证码">
          </van-field>
          <van-button
            round
            color="#1989fa"
            type="primary"
            class="sms"
            @click="getsms"
            >{{ countdown }}</van-button
          >
        </div>
        <van-field
          v-model.trim="password"
          type="password"
          name="密码"
          placeholder="6~15位密码,字母或数字组成"
        />
        <van-field
          v-model.trim="passwords"
          type="password"
          name="密码"
          placeholder="请再次确认密码"
        />
        <div style="margin: 16px 0px">
          <van-button round block type="info" native-type="submit"
            >确认修改</van-button
          >
        </div>
      </van-form>
    </Forms>
  </div>
</template>

<script>
import Forms from "@/components/Forms.vue";
import Titletop from "@/components/Titletop.vue";
export default {
  name: "Myforget",
  components: { Titletop, Forms },
  name: "Myreg",
  data() {
    return {
      password: "",
      passwords:'',
      phone: "",
      sms: "",
      countdown: "获取验证码",
    };
  },
  methods: {
    getsms() {
      if (this.countdown == "获取验证码") {
        if (!this.phone) {
          this.$toast("请输入手机号");
          return;
        }
        this.axios
          .post(" https://jyapi.jyacg.com/web/send_captcha_login", {
            phone: this.phone,
            type: 2,
          })
          .then((res) => {
            if (res.status == 200) {
              if (res.data.code !== 0) {
                this.$toast(res.data.msg);
              } else {
                let a = 60;
                let as = setInterval(() => {
                  this.countdown = a + "s";
                  a--;
                  if (a == 0) {
                    clearInterval(as);
                    this.countdown = "获取验证码";
                  }
                }, 1000);
              }
            }
          });
      }
    },
    onSubmit() {
      if (this.sms && this.countdown !== "获取验证码") {
        
        if (this.password.length >= 6) {
          if(!this.password == this.passwords){
            this.$toast("两次密码不相同");
            return
          }
          this.axios
            .post("https://jyapi.jyacg.com/web/forget_passwd", {
              phone: this.phone,
              password: this.password,
              captcha: this.sms,
            })
            .then((res) => {
              if (res.status == 200) {
                if ((res.data.code == 0)) {
                  this.$router.replace({ name: "login" });
                  localStorage.removeItem('loginpassword')
                } else {
                  this.$toast(res.data.msg);
                }
              }
            });
        } else {
          this.$toast("密码必须是6~15位的数字或字母");
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>

.reg {
  height: 667px;
}
.logo {
  text-align: center;
  img {
    margin-top: 15px;
    width: 113px;
    height: 52px;
  }
}

</style>